export const Api = {
    getCurrencyLayer() {
        return fetch('https://api.exmo.com/v1/ticker/').then(async (result) => {
            //  
            if (result.status === 200 && result.ok) {
                //   JSON
                return result.json()
            }
        }).then((JSON_DATA) => {
            //  
            return JSON_DATA
            //   ...
        }).catch((error) => {
            //  
            console.error('  : ', error);
        })
    }
}